import UserCenterSidebar from '~/components/UserCenterSidebar'
import utils from '~/common/utils'
export default {
  middleware: 'authenticated',
  // components: { UserCenterSidebar },
  async asyncData({ $axios, query }) {
    try {
      await $axios.get('/api/user/verify/forget?token=' + query.token)
      return { success: true, token: query.token }
    } catch (e) {
      return { success: false, message: e.message || '' }
    }
  },
  data() {
    return {
      password: '',
      password2: '',
      captchaId: '',
      captchaUrl: '',
      captchaCode: '',
      token: ''
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.user.current
    },
    isLogin() {
      return !!this.currentUser
    }
  },
  mounted() {
    if (this.isLogin) {
      this.$toast.success('已经处于登录状态', {
        duration: 1000,
        keepOnHover: false,
        position: 'top-center',
        onComplete() {
          utils.linkTo('/user/settings')
        }
      })
    } else {
      this.showCaptcha()
    }
  },
  methods: {
    async showCaptcha() {
      try {
        const ret = await this.$axios.get('/api/captcha/request', {
          params: {
            captchaId: this.captchaId || ''
          }
        })
        this.captchaId = ret.captchaId
        this.captchaUrl = ret.captchaUrl
      } catch (e) {
        this.$toast.error(e.message || e)
      }
    },
    async submitLogin() {
      try {
        if (!this.password && !this.password2) {
          this.$toast.error('请输入密码')
          return
        }
        if (this.password !== this.password2) {
          this.$toast.error('两次密码不一致')
          return
        }
        if (!this.captchaCode) {
          this.$toast.error('请输入验证码')
          return
        }

        const res = await this.$axios.post('/api/user/reset/password', {
          captchaId: this.captchaId,
          captchaCode: this.captchaCode,
          rePassword: this.password2,
          password: this.password,
          token: this.token
        })

        this.$toast.success('密码修改成功！', {
          duration: 1000,
          keepOnHover: false,
          position: 'top-center',
          onComplete() {
            utils.linkTo('/user/signin')
          }
        })
      } catch (e) {
        this.$toast.error(e.message || e)
        await this.showCaptcha()
      }
    }
  }
}
